body {
  margin: 0;
  padding: 0 32px;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
}

#product-list {
  grid-template-columns: repeat(4, 1fr);
  gap: 32px;
  padding: 32px;
  display: grid;
}

.product-card {
  border: 1px solid #ccc;
  border-radius: 16px;
  flex-direction: column;
  display: flex;
  overflow: hidden;
}

.product-card img {
  aspect-ratio: 3 / 2;
  object-fit: cover;
  border-bottom: 1px solid #ccc;
}

.product-card .info {
  flex-direction: column;
  flex: auto;
  align-items: start;
  padding: 16px;
  display: flex;
}

.product-card .category {
  background: #f1f3eb;
  border-radius: 4px;
  margin-bottom: 8px;
  padding: 4px 8px;
}

.product-card .title {
  font-size: 24px;
  font-weight: 600;
}

.product-card .description {
  margin: 0;
  padding: 0;
}

.product-card .metadata {
  padding: 16px;
  display: flex;
}

.product-card .metadata div {
  flex-direction: column;
  flex: 1;
  display: flex;
}

#searchForm {
  justify-content: center;
  align-items: center;
  gap: 16px;
  padding: 32px 0;
  display: flex;
}

#searchForm button, .metadata button {
  letter-spacing: .6px;
  color: #fff;
  cursor: pointer;
  background-color: #598672;
  border: none;
  border-radius: 8px;
  padding: 12px 20px;
  font-size: 14px;
  font-weight: 600;
}

#inputProduct {
  border-radius: 100px;
  width: 60%;
  height: 40px;
  padding-left: 24px;
}
